html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@font-face {
  font-family: 'icomoon';
  src: url("/assets/fonts/icomoon.eot");
  src: url("/assets/fonts/icomoon.eot#iefix") format("embedded-opentype"), url("/assets/fonts/icomoon.ttf") format("truetype"), url("/assets/fonts/icomoon.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

[class^='icon-'],
[class*=' icon-'] {
  font-family: 'icomoon' !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.icon-facebook:before {
  content: '\E900'; }

.icon-instagram:before {
  content: '\E901'; }

.icon-linkedin:before {
  content: '\E902'; }

.icon-twitter:before {
  content: '\E903'; }

/***** General Styles *****/
html, body {
  font-size: 16px;
  line-height: 1;
  font-style: normal;
  font-weight: 400;
  height: 100%;
  background-color: #f9f9f9;
  padding: 0;
  margin: 0; }

*, *:before, *:after {
  box-sizing: inherit; }

a {
  text-decoration: none; }

main.container {
  height: 100%;
  padding: 0;
  margin: 0;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  align-items: center;
  justify-content: center; }
  main.container section.content h1 {
    color: #21211f;
    font-size: 7.35rem;
    padding: 0;
    letter-spacing: -0.1rem;
    font-family: 'Raleway', 'Helvetica Neue', Helvetica, Arial, Sans-Serif;
    font-style: normal;
    font-weight: 900; }
  main.container section.content p {
    color: #21211f;
    font-size: 1.5rem;
    line-height: 0;
    letter-spacing: 0.135rem;
    margin: -0.15rem 0 0 0;
    padding: 0 0 0 25rem;
    font-family: monospace;
    font-style: normal;
    font-weight: 400; }
  main.container section.content a {
    text-align: center;
    margin: 0 auto;
    display: inline-block;
    width: auto;
    font-family: 'Raleway', 'Helvetica Neue', Helvetica, Arial, Sans-Serif;
    font-style: normal;
    font-weight: 300;
    background: #e22a25;
    border-radius: 50px;
    padding: 0.75rem 1.5rem;
    font-size: 0.9rem;
    letter-spacing: 0.25rem;
    text-transform: uppercase;
    color: #fff;
    transition: all 0.5s ease; }
    main.container section.content a:hover {
      background: #21211f;
      transition: all 0.5s ease; }

/***** Footer *****/
footer {
  position: absolute;
  bottom: 35px;
  text-align: center;
  display: block;
  float: left;
  width: 100%; }
  footer a {
    padding: 0 0.5rem;
    color: #21211f;
    font-size: 30px;
    transition: all 0.5s ease; }
    footer a.tw:hover {
      color: #1da1f2;
      transition: all 0.5s ease; }
    footer a.fb:hover {
      color: #3b5998;
      transition: all 0.5s ease; }
    footer a.ig:hover {
      color: #bd0f89;
      transition: all 0.5s ease; }
    footer a.li:hover {
      color: #1a85bc;
      transition: all 0.5s ease; }

/***** Responsiveness *****/
@media all and (max-width: 830px) {
  html,
  body {
    font-size: 14px; } }

@media all and (max-width: 740px) {
  html,
  body {
    font-size: 10px; }
  main.container section.content a {
    margin: 40px auto;
    display: block;
    padding: 1.35rem 1.5rem;
    font-size: 1.4rem; } }

@media all and (max-width: 600px) {
  html,
  body {
    font-size: 9px; }
  main.container section.content a {
    padding: 1.45rem 1.5rem;
    font-size: 1.5rem; }
  footer a {
    font-size: 28px; } }

@media all and (max-width: 480px) {
  html,
  body {
    font-size: 6px; }
  main.container section.content a {
    padding: 1.55rem 1.5rem;
    font-size: 1.6rem; }
  footer a {
    font-size: 25px; } }
